import Scroll from 'base/scroll/scroll'
import { getCommonTools } from 'api/api'
import { mapGetters } from 'vuex'
import Tarbar from 'base/tarbar/tarbarfour'
export default {
  data () {
    return {
      tools: []
    }
  },
  computed: {
    ...mapGetters([
      'userAccount'
    ])
  },
  created () {
    this._getCommonTools()
    this.touch = {}
  },
  methods: {
    _getCommonTools() {
      getCommonTools().then((res) => {
        this.tools = res.data
        // console.log(this.userAccount)
      })
    },
    allOrder() {
      this.$router.push({
        path: '/myorder'
      })
    },
    transTo(item) {
      // 根据item -> link -> platform 的不同，跳转到不同的页面
      let platform = item.link.platform
      switch (platform) {
        case "transToSetting":
          this.$router.push({
            path: '/set'
          })
          break;
        default:
          console.log(123)
      }
    },
    login() {
      this.$router.push({
        path: '/login'
      })
    },
    dragTouchStart(e) {
      this.touch.initiated = true
      this.touch.startX = e.touches[0].pageX
      this.touch.startY = e.touches[0].pageY
      // this.touch.left = this.$refs.drag.clientWidth
    },
    dragTouchMove(e) {
      if (!this.touch.initiated) {
        return
      }
      const deltaX = e.touches[0].pageX - this.touch.startX
      const deltaY = e.touches[0].pageY - this.touch.startY
      if(deltaX < 0) {
        const offsetWidth = Math.min(Math.max(-250, deltaX))
        const offsetHeight = Math.min(Math.max(-300, deltaY))
        this._offset(offsetWidth,offsetHeight)
      }else{
        const offsetWidth = Math.min(Math.min(50, deltaX))
        const offsetHeight = Math.min(Math.max(60, deltaY))
        this._offset(offsetWidth,offsetHeight)
      }
      // console.log(offsetWidth)
    },
    dragTouchEnd(e) {
      this.touch.initiated = false
    },
    _offset(offsetWidth,offsetHeight) {
      this.$refs.drag.style.transform = `translate3d(${offsetWidth}px,${offsetHeight}px,0)`
      // this.$refs.drag.style.transform = `translate3d(${offsetWidth}px,0,0)`
    }
  },
  components: {
    Tarbar,
    Scroll
  }
}



